<template>
  <div class="customerFlow chartPage" @click="closeByDialog">
    <AppHeader>
      <p slot="center">客单客流走势</p>
    </AppHeader>

    <van-tabs v-model="active" animated sticky :offset-top="offsetTop" @click="tabOnClick">
      <van-tab title="客单">
        <div class="contrast">
          <van-tabs v-model="salesActive" @click="salesTabClick">
            <van-tab title="公历同期"></van-tab>
            <van-tab title="农历同期"></van-tab>
          </van-tabs>
        </div>

        <div class="filters">
          <div class="date-filters">
            <input type="text" @click.stop="openByDialog" :value="calendar.display" readonly />
          </div>
          <transition name="fade">
            <div class="calendar-dialog" v-if="calendar.show">
              <div class="calendar-dialog-mask" @click="closeByDialog"></div>
              <div class="calendar-dialog-body">
                <calendar
                  :range="calendar.range"
                  :zero="calendar.zero"
                  :lunar="calendar.lunar"
                  :value="calendar.value"
                  @select="calendar.select"
                ></calendar>
              </div>
            </div>
          </transition>
        </div>

        <div class="myChart-box">
          <div class="myChart" ref="myChart_sku" style="width: 375px;height: 360px"></div>
        </div>

        <div class="chart-text">
          <p>客单均值: {{ totalSum }}</p>
          <p>同期客单均值: {{ tqTotalSum }}</p>
        </div>
      </van-tab>
      <van-tab title="客流">
        <div class="contrast">
          <van-tabs v-model="profitActive" @click="profitTabClick">
            <van-tab title="公历同期"></van-tab>
            <van-tab title="农历同期"></van-tab>
          </van-tabs>
        </div>

        <div class="filters">
          <div class="date-filters">
            <input type="text" @click.stop="openByDialog" :value="calendar2.display" readonly />
          </div>
          <transition name="fade">
            <div class="calendar-dialog" v-if="calendar2.show">
              <div class="calendar-dialog-mask" @click="closeByDialog"></div>
              <div class="calendar-dialog-body">
                <calendar
                  :range="calendar2.range"
                  :zero="calendar2.zero"
                  :lunar="calendar2.lunar"
                  :value="calendar2.value"
                  @select="calendar2.select"
                ></calendar>
              </div>
            </div>
          </transition>
        </div>

        <div class="myChart-box">
          <div class="myChart" ref="myChart_jjxSku" style="width: 375px;height: 360px"></div>
        </div>

        <div class="chart-text">
          <p>客流均值: {{ totalSum }}</p>
          <p>同期客流均值: {{ tqTotalSum }}</p>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Vue from "vue";
import { Tab, Tabs, Calendar, Field } from "vant";

Vue.use(Tab)
  .use(Tabs)
  .use(Calendar)
  .use(Field);

import AppHeader from "@/components/AppHeader.vue";
import calendar from "@/components/calendar/calendar.vue";

import echarts from "echarts/lib/echarts"; // 引入基本模板
import line from "echarts/lib/chart/line"; // 引入线状图组件
import legend from "echarts/lib/component/legend"; // 引入legend组件
import tooltip from "echarts/lib/component/tooltip"; // 引入tooltip组件

export default {
  name: "CustomerFlow",
  components: {
    AppHeader,
    calendar
  },

  data() {
    return {
      active: 0,
      salesActive: 0,
      profitActive: 0,
      screenWidth: document.body.clientWidth, // 屏幕尺寸
      offsetTop: 90 / (750 / document.body.clientWidth),

      calendar: {
        display: "2020/02/16 ~ 2020/03/16", //输入框站位日期
        show: false, //是否显示
        range: true,
        zero: true,
        value: [
          [2018, 2, 16],
          [2019, 2, 16]
        ], //默认选中日期
        lunar: true, //显示农历
        select: (begin, end) => {
          // console.log(begin, end);
          this.calendar_startDate = begin.join("-");
          this.calendar_endDate = end.join("-");
          this.calendar.show = false;
          this.calendar.value = [begin, end];
          this.calendar.display = begin.join("/") + " ~ " + end.join("/");
          this.$nextTick(() => {
            this.get_kd_Chart();
          });
        }
      },
      calendar2: {
        display: "2020/02/16 ~ 2020/03/16", //输入框站位日期
        show: false, //是否显示
        range: true,
        zero: true,
        value: [
          [2018, 2, 16],
          [2019, 2, 16]
        ], //默认选中日期
        lunar: true, //显示农历
        select: (begin, end) => {
          // console.log(begin, end);
          this.calendar2_startDate = begin.join("-");
          this.calendar2_endDate = end.join("-");
          this.calendar2.show = false;
          this.calendar2.value = [begin, end];
          this.calendar2.display = begin.join("/") + " ~ " + end.join("/");
          this.$nextTick(() => {
            this.get_kl_Chart();
          });
        }
      },
      calendar_startDate: "",
      calendar_endDate: "",
      calendar2_startDate: "",
      calendar2_endDate: "",
      getStoreInfo: "",
      sku_myChart: null,
      resultData: [],
      totalSum: 0,
      tqTotalSum: 0
    };
  },
  created() {
    this.get_defaultData();
    this.get_data();
  },
  mounted() {},
  methods: {
    tabOnClick(name, title, index) {
      if (name == 0) {
        if (this.salesActive == 0) {
          this.$nextTick(() => {
            this.drawLine_kd();
            this.totalSum = this.kd_num;
            this.tqTotalSum = this.tqkd_num;
          });
        } else if (this.salesActive == 1) {
          this.$nextTick(() => {
            this.drawLine_nlkd();
            this.totalSum = this.kd_num;
            this.tqTotalSum = this.nlkd_num;
          });
        }
      } else if (name == 1) {
        if (this.profitActive == 0) {
          this.$nextTick(() => {
            this.drawLine_kl();
            this.totalSum = this.kl_num;
            this.tqTotalSum = this.tqkl_num;
          });
        } else if (this.profitActive == 1) {
          this.$nextTick(() => {
            this.drawLine_nlkl();
            this.totalSum = this.kl_num;
            this.tqTotalSum = this.nlkl_num;
          });
        }
      }
    },
    salesTabClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_kd();
          this.totalSum = this.kd_num;
          this.tqTotalSum = this.tqkd_num;
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_nlkd();
          this.totalSum = this.kd_num;
          this.tqTotalSum = this.nlkd_num;
        });
      }
    },
    profitTabClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_kl();
          this.totalSum = this.kl_num;
          this.tqTotalSum = this.tqkl_num;
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_nlkl();
          this.totalSum = this.kl_num;
          this.tqTotalSum = this.nlkl_num;
        });
      }
    },
    get_defaultData() {
      this.getStoreInfo = this.$store.getters.getStoreInfo; //获取当前选择的门店信息
      // console.log(this.$store.state.defaultDate);
      const time1 = this.formatDate.pastDate(
        this.$store.state.defaultDate + 1,
        "-"
      );
      const time2 = this.formatDate.pastDate(2, "-");
      this.calendar_startDate = time1; //日历默认开始时间
      this.calendar_endDate = time2; //日历默认结束时间
      this.calendar.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");

      this.calendar2_startDate = time1; //日历默认开始时间
      this.calendar2_endDate = time2; //日历默认结束时间
      this.calendar2.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar2.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray = this.dateArray2 = this.formatDate.dateArray(
        this.calendar_startDate,
        this.calendar_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          //客单
          const theDay_kd = [];
          const contemporary_tqkd = [];
          const contemporary_nlkd = [];

          //客流
          const theDay_kl = [];
          const contemporary_tqkl = [];
          const contemporary_nlkl = [];
          resultData.forEach(item => {
            //客单
            if (item.guestList) {
              theDay_kd.push(item.guestList);
            } else {
              theDay_kd.push("0");
            }
            if (item.guestListSame) {
              contemporary_tqkd.push(item.guestListSame);
            } else {
              contemporary_tqkd.push("0");
            }
            if (item.nguestListSame) {
              contemporary_nlkd.push(item.nguestListSame);
            } else {
              contemporary_nlkd.push("0");
            }

            //客流
            if (item.passengerFlow) {
              theDay_kl.push(item.passengerFlow);
            } else {
              theDay_kl.push("0");
            }
            if (item.passengerFlowSame) {
              contemporary_tqkl.push(item.passengerFlowSame);
            } else {
              contemporary_tqkl.push("0");
            }
            if (item.npassengerFlowSame) {
              contemporary_nlkl.push(item.npassengerFlowSame);
            } else {
              contemporary_nlkl.push("0");
            }
          });
          this.resultData = resultData;

          //客单
          this.theDay_kd = theDay_kd;
          this.contemporary_tqkd = contemporary_tqkd;
          this.contemporary_nlkd = contemporary_nlkd;
          var kd_num = 0;
          var tqkd_num = 0;
          var nlkd_num = 0;
          theDay_kd.forEach(item => {
            kd_num += parseFloat(item);
          });
          contemporary_tqkd.forEach(item => {
            tqkd_num += parseFloat(item);
          });
          contemporary_nlkd.forEach(item => {
            nlkd_num += parseFloat(item);
          });
          this.kd_num = kd_num.toFixed(2);
          this.tqkd_num = tqkd_num.toFixed(2);
          this.nlkd_num = nlkd_num.toFixed(2);
          this.totalSum = this.kd_num;
          this.tqTotalSum = this.tqkd_num;
          this.drawLine_kd();

          //客流
          this.theDay_kl = theDay_kl;
          this.contemporary_tqkl = contemporary_tqkl;
          this.contemporary_nlkl = contemporary_nlkl;
          var kl_num = 0;
          var tqkl_num = 0;
          var nlkl_num = 0;
          theDay_kl.forEach(item => {
            kl_num += parseFloat(item);
          });
          contemporary_tqkl.forEach(item => {
            tqkl_num += parseFloat(item);
          });
          contemporary_nlkl.forEach(item => {
            nlkl_num += parseFloat(item);
          });
          this.kl_num = kl_num.toFixed(2);
          this.tqkl_num = tqkl_num.toFixed(2);
          this.nlkl_num = nlkl_num.toFixed(2);
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    get_kd_Chart() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray = this.formatDate.dateArray(
        this.calendar_startDate,
        this.calendar_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          //客单
          const theDay_kd = [];
          const contemporary_tqkd = [];
          const contemporary_nlkd = [];
          resultData.forEach(item => {
            //客单
            if (item.guestList) {
              theDay_kd.push(item.guestList);
            } else {
              theDay_kd.push("0");
            }
            if (item.guestListSame) {
              contemporary_tqkd.push(item.guestListSame);
            } else {
              contemporary_tqkd.push("0");
            }
            if (item.nguestListSame) {
              contemporary_nlkd.push(item.nguestListSame);
            } else {
              contemporary_nlkd.push("0");
            }
          });
          this.resultData = resultData;

          //客单
          this.theDay_kd = theDay_kd;
          this.contemporary_tqkd = contemporary_tqkd;
          this.contemporary_nlkd = contemporary_nlkd;
          var kd_num = 0;
          var tqkd_num = 0;
          var nlkd_num = 0;
          theDay_kd.forEach(item => {
            kd_num += parseFloat(item);
          });
          contemporary_tqkd.forEach(item => {
            tqkd_num += parseFloat(item);
          });
          contemporary_nlkd.forEach(item => {
            nlkd_num += parseFloat(item);
          });
          this.kd_num = kd_num.toFixed(2);
          this.tqkd_num = tqkd_num.toFixed(2);
          this.nlkd_num = nlkd_num.toFixed(2);
          if (this.salesActive == 0) {
            this.$nextTick(() => {
              this.drawLine_kd();
              this.totalSum = this.kd_num;
              this.tqTotalSum = this.tqkd_num;
            });
          } else {
            this.$nextTick(() => {
              this.drawLine_nlkd();
              this.totalSum = this.kd_num;
              this.tqTotalSum = this.nlkd_num;
            });
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    get_kl_Chart() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray2 = this.formatDate.dateArray(
        this.calendar2_startDate,
        this.calendar2_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar2_startDate,
        endDate: this.calendar2_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          //客流
          const theDay_kl = [];
          const contemporary_tqkl = [];
          const contemporary_nlkl = [];
          resultData.forEach(item => {
            //客流
            if (item.passengerFlow) {
              theDay_kl.push(item.passengerFlow);
            } else {
              theDay_kl.push("0");
            }
            if (item.passengerFlowSame) {
              contemporary_tqkl.push(item.passengerFlowSame);
            } else {
              contemporary_tqkl.push("0");
            }
            if (item.npassengerFlowSame) {
              contemporary_nlkl.push(item.npassengerFlowSame);
            } else {
              contemporary_nlkl.push("0");
            }
          });
          this.resultData = resultData;

          //客流
          this.theDay_kl = theDay_kl;
          this.contemporary_tqkl = contemporary_tqkl;
          this.contemporary_nlkl = contemporary_nlkl;
          var kl_num = 0;
          var tqkl_num = 0;
          var nlkl_num = 0;
          theDay_kl.forEach(item => {
            kl_num += parseFloat(item);
          });
          contemporary_tqkl.forEach(item => {
            tqkl_num += parseFloat(item);
          });
          contemporary_nlkl.forEach(item => {
            nlkl_num += parseFloat(item);
          });
          this.kl_num = kl_num.toFixed(2);
          this.tqkl_num = tqkl_num.toFixed(2);
          this.nlkl_num = nlkl_num.toFixed(2);
          if (this.profitActive == 0) {
            this.$nextTick(() => {
              this.drawLine_kl();
              this.totalSum = this.kl_num;
              this.tqTotalSum = this.tqkl_num;
            });
          } else {
            this.$nextTick(() => {
              this.drawLine_nlkl();
              this.totalSum = this.kl_num;
              this.tqTotalSum = this.nlkl_num;
            });
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    openByDialog() {
      this.calendar.show = true;
      this.calendar2.show = true;
    },
    closeByDialog() {
      this.calendar.show = false;
      this.calendar2.show = false;
    },
    //画图
    drawLine_kd() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_sku);
      let dateArray = this.dateArray;
      let theDay = this.theDay_kd;
      let contemporary = this.contemporary_tqkd;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["客单", "同期客单"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "客单",
            type: "line",
            data: theDay
          },
          {
            name: "同期客单",
            type: "line",
            data: contemporary
          }
        ]
      });
    },
    drawLine_nlkd() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_sku);
      let dateArray = this.dateArray;
      let theDay = this.theDay_kd;
      let contemporary = this.contemporary_nlkd;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["客单", "同期客单"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "客单",
            type: "line",
            data: theDay
          },
          {
            name: "同期客单",
            type: "line",
            data: contemporary
          }
        ]
      });
    },
    drawLine_kl() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_jjxSku);
      let dateArray2 = this.dateArray2;
      let theDay = this.theDay_kl;
      let contemporary = this.contemporary_tqkl;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["客单", "同期客单"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray2
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "客单",
            type: "line",
            data: theDay
          },
          {
            name: "同期客单",
            type: "line",
            data: contemporary
          }
        ]
      });
    },
    drawLine_nlkl() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_jjxSku);
      let dateArray2 = this.dateArray2;
      let theDay = this.theDay_kl;
      let contemporary = this.contemporary_nlkl;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["客流", "同期客流"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray2
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "客流",
            type: "line",
            data: theDay
          },
          {
            name: "同期客流",
            type: "line",
            data: contemporary
          }
        ]
      });
    }
  }
};
</script>

